import React, { Suspense } from "react";
const Author = React.lazy(() => import("../../../Elements/Author/Author"));
const Time = React.lazy(() => import("../../../Elements/Time/Time"));
const Network = React.lazy(() => import("../../../Elements/Network/Network"));
const Seprator = React.lazy(() => import("../../../Elements/Seprator/Seprator"));

const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return <div className="tb_g_p_author_wrapper">
    <div className="tb_g_p_author">
      {postAuthor ? <Suspense fallback={<></>}><Author author={author} network={network} authorClass={'tb_g_p_author_profile'} /></Suspense> : ''}
      <div className="tb_g_p_author_info">
        {postAuthor ? <div className="tb_g_p_authorname">{author.name}</div> : ''}
        <div className="tb_g_p_post_info">
          {postAuthor ? <div className="tb_g_p_username">{username}</div> : ''}
          {authorSeprator ? <Suspense fallback={<></>}><Seprator sepClass={`tb_g_p_seprator`} /></Suspense> : ''}
          {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_g_p_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> </Suspense> : ''}
        </div>
      </div>
    </div>
      <div className="tb_g_p_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_g_p_social__ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div>  
  </div>
}
export default AuthorDetails